.footer
{
    position:fixed;
    display:flex;
    justify-content: space-between;
    bottom:0;
    width:97%;
    height:65px;
    background-color: #282828;
    padding:20px;
}

.footer_right * .MuiSlider-root
{
    color:green
}
.footer_left
{
    flex:0.3;
    display:flex;
    align-items: center;
    color:white;
    max-width:300px;
}

.footer_albumlogo
{
    height:60px;
    width:60px;
    margin-right:10px;
    object-fit: contain;
}

.footer_green
{
    color:#1ed15e;
}

.footer_songinfo>h4
{
    margin-bottom: 5px;
}

.footer_songinfo>p
{
   font-size:12px;
}

.footer_center
{
    flex:0.4;
    padding:0 100px;
    color:white;
    display:flex;
    align-items:center;
    justify-content: space-between;
    max-width: 300px;
}

.footer_icon:hover,
.footer_green:hover
{
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
}

.footer_right
{
    flex:0.3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}