.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
    overflow: hidden;
  }
  
  .login > img {
    max-width: 100%;
    height: auto;
  }
  
  .login > a {
    padding: 20px;
    border-radius: 99px;
    background-color: #1db954;
    font-weight: 800;
    color: white;
    text-decoration: none;
  }
  